@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');

/*HEADER*/
.header {
    margin: 0; 
    box-sizing: border-box;
    text-transform: capitalize;
    text-decoration: none;
    position: fixed;
    top: 0; left: 0; right: 0;
    background-color: var(--text-color-inverse);
    box-shadow:  0 4px 5px var(--navbar-shallow-color);
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-family: 'Roboto', sans-serif;
    text-transform: uppercase;
   }


 
  /*LOGO*/    
     

.header .logo {
   display: flex;
   flex-direction: row-reverse;
}
.header .logo div {
    font-weight: bolder;
    font-size: 2rem;
    color: var(--primary-color);
    display: flex;
    align-items: center;
    line-height: .6;
    font-family: 'Roboto', sans-serif;
    margin-left: 5px;
   }

   .header .logo div span {
      font-size: 20px;
      align-self: flex-end;
      font-weight: 600;
      margin-bottom: 9px;
      margin-left: 5px;
   }
  
.header .logo img {
    width: 40px;
    border-radius: 50%;
    margin-left: 10px;

 }



 /*NAVBAR*/
.navbar {
    margin-right: 20px;
}
 
 #ul {
    margin: 0;
    display: flex;
    justify-content: center;
}
 
.navbar ul li {
   
    list-style-type: none;
    margin-right: 20px;
    font-size: 1.3rem;

}

 .navbar a {
    display: block;
    padding: 15px;
    cursor: pointer;
    border-radius: 3px;
    transition: 0.5s;

 }
   
   /*DROPDOWN*/

 .dropdown {
    position: relative;
    display: inline-block;
 }

 .dropdown-link{
  
    
    border: none;
    cursor: pointer;
 }

 .dropdown-content{
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 200px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    margin: 0;
    padding: 0;
    }
 .dropdown-content li {
    width: 100%;
    text-align: center;
 }

 .dropdown-content li a {
    color: black;
    text-decoration: none;
    display: block;
 }

 .dropdown:hover .dropdown-content {
    display: block;
}

.navbar a:not(.active):hover {
    color: var(--primary-color);
    transform:translateY(-7px);
    transition: all 0.4s;
}

.active {
  color:var(--primary-color);
 
}

/*Responsive-Menu*/

.menu {
   display: none;
   height: 50px;
   width: 50px;
   
  
   position: absolute;
   top: 0.285rem;
   right: 1.5rem;
}
#img {
   width: 100%;
   height: 100%;
   transition: 1s;
   
   background-image: url(../images/3-removebg-preview.png);
   background-size: cover;
}

@media (max-width: 950px) {
  
  
  .header {
   flex-direction: column;
   }
   
   .menu {
      display: block;
   }

  .logo {
   align-self: flex-start;
  }

  .logo div{
   font-weight: 500;
  }
   
  .header .navbar {
    width: 100%;
    margin: 0;
   }

  #ul{
   position: absolute; 
   right: 0.02rem;
   display: flex;
   flex-direction: column;
   padding: 0;
   transform: translateY(95rem);
   transition: 1s;
   height: 30rem;
   width: 100%;
   background-color: #fff;
   margin-top: 10px;
   box-shadow:  0 4px 5px var(--navbar-shallow-color);
   }

   #ul li {
      text-align: center;
      margin: 15px;
   }

   #ul li{
      font-size: 1.3rem;
  
   }
  #ul.open {
   transform: none;
  }

  #img.change {
   background-image: url(../images/1.1-removebg-preview.png);
    transition: 1s;
  }

 .navbar a.active{
   background-color: var(--primary-color);
   color: var(--text-color-inverse);
   transition: all 0.4s;
   transform: none;
 } 
  
 .dropdown:hover .dropdown-content {
   display: none;
}

.navbar a:not(.active):hover {
   background-color: var(--primary-color);
   color: var(--text-color-inverse);
   transform:none;
  
}
}
