.panel-container {
width: 100%;
height: 25rem;
display: flex;
align-items: center;
justify-content:center;
color: var(--text-color-inverse);
background-size: cover;
background-position: center;
margin-bottom: 20px;
}

.panel-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.589);
    padding: 20px;
}

.panel-container .panel-text h2 {
    font-size: 2rem;
    margin-bottom: 5px;
}
.panel-container .panel-text p {
    max-width: 500px;
    line-height: 1.2;
}
.panel-container .panel-text a {
    border: 1px solid var(--text-color-inverse);
    margin-top: 10pxx;
    border-radius: 3px;
    padding: 5px 15px 7px 15px; 
    color: var(--text-color-inverse);
}

.panel-container .panel-text a:hover {
    background-color: var(--text-color-inverse);
    color: rgb(72, 72, 73);
    transition: 0.4s;
}