 .autext-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: var(--text-color);
 }
 
 .box-space {
    padding: 35px;
 }
 .autext-container h2 {
    text-align: center;
    margin-top: 30px;
    font-size: 2rem;
    color: var(--text-color);
}

.autext-container ul {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 20px;
   

}

.autext-container ul li {
    font-size: 1.3rem;
    align-self: flex-start;
    margin-top: 0;
    margin-left: 40px;
    max-width: 700px;
    font-weight: 700;
}

.autext-container ul p{
    max-width: 1000px;
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 60px;
    line-height: 1.7;
}

@media (max-width: 950px) {
    .autext-container ul {
        width: 80%;
    }
    .box-space {
        padding: 30px;
    }
}