.banner-container {
   display: flex;
   align-items: center;
   justify-content: center;
   width: 100%;
   height: 500px;
   background-image: url(../images/mech-mind-robotics-sTzb90A1Abs-unsplash.jpg);
   background-repeat: no-repeat;
   background-position: center ;
   background-size: cover;
   
    }

     .banner-text {
      display: flex;
      flex-direction: column;
     
     margin-top: 40px;
    
     background-color: rgba(0, 0, 0, 0.589);
     padding: 30px;

  }
   .banner-text h1 {
      color: var(--text-color-inverse);
      font-size: 2rem;
      font-weight: bold;
      margin: 0;
      margin-bottom: 10px;
      
     
   }
  
   .banner-text p {
      font-size: 1.6rem;
      color: var(--text-color-inverse);
      max-width: 800px;
      line-height: 1.3;
      margin: 0;
   }
   
   
   @media (min-width: 950px) {
     
     .banner-text {
        margin-left: 0px;
        margin-right: 70px;
     }
     
     .banner-container .banner-text h1 {
        font-size: 3rem;
     }
  
     .banner-text p {
        font-size: 2rem;
     }
  
     
   
   }