.main-services {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
 
.main-services h2 {
    font-size: 42px;
    font-weight: bold;
    margin-top: 0;
    color: #383838;
    font-family: Roboto, sans-serif;
}

.service-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}


.service-container ul {
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    justify-content: center;
    padding: 0;
 

}
.service-container ul li {
    width: 250px;
    height: 250px;
    text-align: center; margin: 30px;
}


.service-container a {
   text-align: center;
    font-weight: 500;
    font-size: 24px;
    color: #434343;
    font-family: Roboto, sans-serif;
 }


.service-container a img {
    width: 80%;
    margin-bottom: 20px;
    }

    .service-container a div:hover {
        text-decoration: underline;
        text-decoration-thickness: 3px;
       

    }

    @media (max-width: 950px) {

        .service-container ul li {
            width: 150px;
            height: 150px;
            text-align: center; margin: 30px;
        }
        
        .service-container a img {
            width:100%;
        }

        
    .service-container a div:hover {
        text-decoration: underline;
     }

    }