.about-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 25px;
    width: 100%;
    padding-bottom: 50px;
 }
  
 .about-container hr {
    display: block;
    background-color: var(--primary-color);
    width: 80%;
    height: 2px;
    border-radius: 40px;
    margin-top: 30px;
 }

 .about-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-weight: 500;
 }

 .about-info p {
     width:80%;
    margin-top: 0;
    line-height: 2;
    font-size: 20px;
 }


 
 