.main-contact {
    display: flex;
    justify-content: center;
    margin-top: 50px;


}

.contact-container {
    background-color: rgb(36, 35, 35);
    padding: 50px 100px 100px 100px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var( --primary-color);
    font-size: 30px;
    font-family: Roboto, sans-serif;
    
}

.contact-container a {
    color: rgb(36, 35, 35);
    background-color: var(--primary-color);
    padding: 18px 65px;
    border-radius: 5px;
    font-weight: 600;
    font-size: 24px;
}
.contact-container a:hover{
    background-color: rgb(36, 35, 35);
    border: 3px solid var(--primary-color);
    color: var(--primary-color);
    transition: all 0.5s;

    
}
@media (max-width: 950px) {
    .main-contact{
        margin-top: 80px;
    }
    .contact-container{
        font-size: 24px;
        
    }

    .contact-container h2 {
        margin-left: 40px;
    }
    .contact-container a {
        font-size: 16px;
    }

}